import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "@/views";

const ChineseView = () => import("@/views/Chinese");
const EnglishView = () => import("@/views/English");
const MobileView = () => import("@/views/Mobile");

const Home = () => import('@/views/Chinese/Home');

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        component: HomeView,
        name: "Home",
        meta: {
            title: "山河大学社科部 - Shanhe Social science",
            type: "PC"
        },
    },
    {
        path: "/zh-cn",
        component: ChineseView,
        meta: {
            title: "首页 - 山河大学社科部",
            type: "PC"
        },
        children: [
            {
                path: "/zh-cn",
                component: Home,
                title: "首页 - 山河大学社科部",
                type: "PC"
            }
        ]
    },
    {
        path: "/en",
        component: EnglishView,
        meta: {
            title: "Home - Shanhe Social science",
            type: "PC"
        },
    },
    {
        path: "/mobile",
        component: MobileView,
        meta: {
            title: "山河大学社科部 - Shanhe Social science",
            type: "Mobile"
        }
    }
];

const router = new VueRouter({
    routes,
});

router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(window.navigator.userAgent);
    if (isMobile && !to.path.startsWith('/mobile/')) {
        next('/mobile' + to.fullPath);
    } else if (!isMobile && to.path.startsWith('/mobile/')) {
        next(to.fullPath.replace('/mobile', ''));
    } else {
        next();
    }
});

export default router;